


































import { Component, Emit, Prop, Vue } from 'vue-property-decorator';

@Component({})
export default class Confirmation extends Vue {
  @Prop({ required: true })
  identifier!: number;
  @Prop({ required: true })
  positiveAction!: string;

  @Emit()
  public cancel() {}

  @Emit()
  public confirm() {}
}
